.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #1d1e23;
  color: #f0f8ff;
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  width: 60vw;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  padding-top: 20px;
  color: white;
}

.App-link {
  color: #09d3ac;
}

.App input {
  background: none;
  border: 3px solid #a0a0a0;
  border-radius: 12px;
  padding: 6px;
  margin-right: 10px;
  text-align: center;
  color: #fff;
}

.money {
  position: relative;
  width: 90%;
  margin: 0 auto 30px;
}

.money svg {
  width: 100%;
}

.money .label {
  position: absolute;
  top: 0;
  width: 100%;
  color: #000;
  font-size: 2.8vw;
  font-weight: 700;
}

@media (max-width: 650px ) {
  .money .label {
    font-size: 20px;
  }

  .money svg {
    height: 28px;
  }

  .App .CircularProgressbar {
    width: 70%;
  }
}